import { Container, Image } from "react-bootstrap";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import map from "../Colors/map.png"

function Delivery(){
    return(
        <div id="delivery">
             <h1 className='mb-5 text-center py-3 bg-yellow section'></h1>
             <Container fluid className="my-5 py-5">
                <Row>
                    <Col className="text-center">
                        <h1 className="text-yellow px-4 pb-3">   </h1>
                        <h4 className="text-white"> ,    ,   .</h4>
                        <div className="border-top w-50 mx-auto my-5"></div>
                        <p className="text-white mx-auto">   ,   ,<br/>  :)</p>
                    </Col>
                    <Col sm={5}>
                        <Image fluid src={map}/>
                    </Col>
                </Row>
             </Container>
        </div>
    )

}
export default Delivery